/**
 *    -   
 * 27  DOM  React 
 */
import React from 'react';

/**
 * @param {object} props
 * @param {string} props.className -   (palette, size )
 * @param {ReactNode} props.icon -  
 * @param {string} props.label -  
 * @param {Function} props.onClick -  
 * @param {Function} props.onKeyDown -  
 * @param {string} props.dataText -   
 * @param {boolean} props.disabled -  
 * @param {object} props.style -  
 */
const Button = ({
  className = '',
  icon = null,
  label,
  onClick,
  onKeyDown,
  dataText,
  disabled = false,
  style = {},
  children,
  ...rest
}) => {
  const buttonClasses = `button ${className}`.trim();

  return (
    <button
      className={buttonClasses}
      data-text={dataText}
      onClick={onClick}
      onKeyDown={onKeyDown}
      disabled={disabled}
      aria-disabled={disabled}
      style={style}
      {...rest}
    >
      <div className="background dynamic">
        {icon && (
          <span className="content icon" aria-hidden="true">
            {icon}
          </span>
        )}
        {label && (
          <span className="content label">{label}</span>
        )}
        {children}
      </div>
    </button>
  );
};

export default Button;

