export const Summary = () => {
  const {
    sections,
    isDark,
    totalCount,
    totalSum,
    isResetModal,
    setisResetModal,
    convertToKoreanQuantity,
    volume,
    currentPage,
    setCurrentPage
  } = useContext(AppContext);
  const path = currentPage;
  const [isDisabledBtn, setisDisabledBtn] = useState(true);
  const { handleText } = useTextHandler(volume);

  useEffect(() => {
    if (totalCount > 0) setisDisabledBtn(false);
    else setisDisabledBtn(true);
  }, [totalCount]);

  if (path !== "second" && path !== "third") {
    return null;
  }

  return (
    <div className="summary">
        <div className="task-manager">
          <p className="summary-label"></p>
          <p className="summary-text">{totalCount}</p>
          <div className="short-colline"></div>
          <p className="summary-label"></p>
          <p className="summary-text">
            {totalSum.toLocaleString("ko-KR")}
          </p>
        </div>
        <div
          className="flex"
          ref={sections.footer}
          data-text={`, , ${convertToKoreanQuantity(totalCount)} , , ${totalSum.toLocaleString("ko-KR")},  ,`}
        >
          {path === "second" && (
            <>
              <button
                data-text=","
                className="button summary-btn"
                onClick={(e) => { 
                  e.preventDefault();
                  setisResetModal(true);
                 }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {setisResetModal(true);}, 100);
                  }
                }}
              >
                <div className="background dynamic">
                  <span className="content icon" aria-hidden="true">
                    <img
                      className="summary-btn-icon"
                      src={"/images/ico_reset.png"}
                      alt=""
                    />
                  </span>
                  <span className="content label"></span>
                </div>
              </button>
              <button
                data-text={`,  ${isDisabledBtn ? "" : ""}`}
                className={`button summary-btn ${
                  isDisabledBtn ? "disabled" : ""
                }`}
                aria-disabled={isDisabledBtn}
                onClick={(e) => { 
                  e.preventDefault();
                  e.target.focus();
                  if (totalCount > 0) {
                    path === "second"
                      ? setCurrentPage("third")
                      : setCurrentPage("forth");
                  }
                 }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      if (totalCount > 0) {
                      path === "second"
                        ? setCurrentPage("third")
                        : setCurrentPage("forth");
                    }}, 300);
                  }
                }}
              >
                <div className="background dynamic">
                  <span className="content icon" aria-hidden="true">
                    <img
                      className="summary-btn-icon"
                      src={
                        isDark
                          ? "/images/contrast_ico_order.png"
                          : "/images/ico_order.png"
                      }
                      alt=""
                    />
                  </span>
                  <span className="content label"></span>
                </div>
              </button>
            </>
          )}

          {path === "third" && (
            <>
              <button
                data-text=" ,"
                className="button summary-btn"
                onClick={(e) => { 
                  e.preventDefault();
                  setCurrentPage("second");
                 }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => { setCurrentPage("second");}, 300);
                  }
                }}
              >
                <div className="background dynamic">
                  <span className="content icon" aria-hidden="true">
                    <img
                      className="summary-btn-icon"
                      src={"/images/ico_add_order.png"}
                      alt=""
                    />
                  </span>
                  <span className="content label"></span>
                </div>
              </button>
              <button
                data-text=", "
                className="button summary-btn"
                onClick={(e) => { 
                  e.preventDefault();
                  e.target.focus();
                  if (totalCount > 0) {
                    path === "second"
                      ? setCurrentPage("third")
                      : setCurrentPage("forth");
                  }
                 }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      if (totalCount > 0) {
                        path === "second"
                          ? setCurrentPage("third")
                          : setCurrentPage("forth");
                      }}, 300);
                  }
                }}
              >
                <div className="background dynamic">
                  <span className="content icon" aria-hidden="true">
                    <img
                      className="summary-btn-icon"
                      src={
                        isDark
                          ? "/images/contrast_Mask group.png"
                          : "/images/Mask group.png"
                      }
                      alt=""
                    />
                  </span>
                  <span className="content label"></span>
                </div>
              </button>
            </>
          )}
        </div>
      </div>
  );
};

export const Bottom = () => {
  const {
    sections,
    isDark,
    isReturnModal,
    setisReturnModal,
    isAccessibilityModal,
    setisAccessibilityModal,
    isCreditPayContent,
    volume,
    currentPage
  } = useContext(AppContext);
  const path = currentPage;
  const { handleText } = useTextHandler(volume);

  return (
    <div
      className="bottom"
        data-text={
          path === "" || (path === "forth" &&  [1,2,3].includes(isCreditPayContent)) ? " ,  ," : " ,   ,"
        }
        ref={sections.bottomfooter}
      >
        {path === "" || (path === "forth" &&  [1,2,3].includes(isCreditPayContent)) ? (
          <div className="footer-coffeelogo"></div>
        ) : (
          <button
            className="button down-footer-button btn-home"
            data-text=","
            onClick={(e) => { 
              e.preventDefault();
              setisReturnModal(true);
              document.activeElement.blur();
             }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  setisReturnModal(true);
                  document.activeElement.blur();
                  }, 300);
              }
            }}
          >
            <div className="background dynamic">
              <span className="content icon" aria-hidden="true">
                <div className="div-footer-circle">
                  <img
                    className="black-circle"
                    src="images/home_btn.png"
                    alt="home"
                  />
                </div>
              </span>
              <span className="content label"></span>
            </div>
          </button>
        )}

        <button 
          data-text={path === "" ? "," : ","}
          className="button down-footer-button"
          onClick={(e) => { 
            e.preventDefault();
            setisAccessibilityModal(true)
           }}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              e.preventDefault();
              handleText(', ', false);
              setTimeout(() => {
                setisAccessibilityModal(true)
                }, 300);
            }
          }}
        >
          <div className="background dynamic">
            <span className="content icon" aria-hidden="true">
              <div className="div-footer-circle">
                <img
                  className="black-circle"
                  src="/images/contrast_ico_low_sc.png"
                  alt="wheelchair"
                />
              </div>
            </span>
            <span className="content label"></span>
          </div>
        </button>
      </div>
  );
};

