import React, { useContext, useState, useEffect } from "react";
import { AppContext } from "../context/AppContext";
// import { startReturnTimer, updateTimer } from "../assets/timer";
import { useKeyboardNavigation } from "../assets/useKeyboardNavigation";
import { useTextHandler } from "../assets/tts";

const ThirdPage = () => {
  const {
    sections,
    totalMenuItems,
    isDark,
    isLow,
    quantities,
    handleIncrease,
    handleDecrease,
    filterMenuItems,
    isDeleteModal,
    setisDeleteModal,
    isDeleteCheckModal,
    setisDeleteCheckModal,
    setDeleteItemId,
    commonScript,
    volume,
    convertToKoreanQuantity,
    setCurrentPage
  } = useContext(AppContext);
  const { handleText } = useTextHandler(volume);
  //    

  const ITEMS_PER_PAGE = isLow ? 3 : 6; //   
  const [pageNumber, setPageNumber] = useState(1);

  //     
  const startIndex = (pageNumber - 1) * ITEMS_PER_PAGE;
  const priceItems = filterMenuItems(totalMenuItems, quantities);

  const currentItems = priceItems.slice(
    startIndex,
    startIndex + ITEMS_PER_PAGE
  );
  //    
  const totalPages =
    priceItems.length === 0 ? 1 : Math.ceil(priceItems.length / ITEMS_PER_PAGE);

  //   
  const handlePrevPage = () => {
    if (pageNumber > 1) {
      setPageNumber(pageNumber - 1);
    }
  };

  const handleNextPage = () => {
    if (pageNumber < totalPages) {
      setPageNumber(pageNumber + 1);
    }
  };
  
  function prependRows(existingArray, currentItems) {
    // row1 rowN  
    const newRows = Array.from(
      { length: currentItems },
      (_, index) => `row${index + 1}`
    );

    //      
    return ["page", ...newRows, ...existingArray];
  }

  const focusableSections = prependRows(
    ["bottom", "footer", "bottomfooter"],
    currentItems.length
  );

  // useKeyboardNavigation
  const { updateFocusableSections } = useKeyboardNavigation({
    initFocusableSections: focusableSections,
    initFirstButtonSection: "row1",
  });

  const handleTouchDecrease = (id) => {
    if (quantities[id] === 1) {
      setDeleteItemId(id);
      if (currentItems.length > 1) {
        setisDeleteModal(true);
      } else {
        setisDeleteCheckModal(true);
      }
    } else {
      handleDecrease(id);
    }
  }

  const handleTouchDelete = (id)=>{
    setDeleteItemId(id);
    if (currentItems.length > 1) {
      setisDeleteModal(true);
    } else {
      setisDeleteCheckModal(true);
    }
  }


  useEffect(()=>{
     //   focusableSections 
     const updatedSections = prependRows(
      ["bottom", "footer", "bottomfooter"],
      currentItems.length
    );
    updateFocusableSections(updatedSections); //  

  },[pageNumber])

  useEffect(() => {
    if (currentItems.length === 0) {
      setCurrentPage("second");
    }
  }, [currentItems]);

  useEffect(() => {
    //      
    const timer = setTimeout(() => {
      if (document.activeElement) {
        const pageTTS = document.activeElement.dataset.text;
        setTimeout(() => {
          handleText(pageTTS);
        }, 500); // ""     0.5  
      }
      // startReturnTimer(commonScript.return, handleText, navigate);
    }, 0);

    return () => clearTimeout(timer); // 
    //   ButtonStyleGenerator.calculateButtonSizes() 
  }, []);



  return (
    <>
      <div className="main third">
        <div className="title">
          <span
            style={isDark ? { color: "#FFE101" } : { color: "#8C532C" }}
          >
            
          </span>
           &nbsp;
          <span
            style={isDark ? { color: "#FFE101" } : { color: "#8C532C" }}
          >
            
          </span>
          &nbsp; 
        </div>
        <div className="third-middle-content">
          {isLow ? (
            <>
              <p className="third-middle-content-header"></p>
              <p className="third-middle-content-header-qty"></p>
              <p className="third-middle-content-header-price"></p>
              <p className="third-middle-content-header-delete"></p>
            </>
          ) : (
            <>
              <p className="third-middle-content-header-normal"></p>
              <p className="third-middle-content-header-qty-normal"></p>
              <p className="third-middle-content-header-price-normal"></p>
              <p className="third-middle-content-header-delete-normal"></p>
            </>
          )}
        </div>
        <div className="third-main-content">
          {currentItems.map((item, i) => {
            const globalIndex = startIndex + i + 1;
            const rowIndex = (i % ITEMS_PER_PAGE) + 1;
            const refKey = `row${rowIndex}`;
            return (
              <div key={item.id}>
                <div className="order-item" ref={sections[refKey]} data-text={`,${globalIndex}, ${item.name}, ${convertToKoreanQuantity(quantities[item.id])} , ${item.price * quantities[item.id]},   , `}>
                  <div
                    className="order-image-div"
                  >
                    <div className="order-index">{globalIndex}</div>
                    <img
                      src={item.img}
                      alt={item.name}
                      className="order-image"
                    />
                  </div>

                  <p className="order-name">{item.name}</p>
                  <div className="order-quantity">
                    <button
                      data-text=" "
                      className="button qty-btn"
                      onClick={(e) => {
                        e.preventDefault();
                        e.currentTarget.focus();
                        handleTouchDecrease(item.id);
                      }}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          e.preventDefault();
                          handleText('', false);
                          setTimeout(() => handleTouchDecrease(item.id), 100);
                        }
                      }}
                    >
                      <div className="background dynamic">
                        <span className="content label">-</span>
                      </div>
                    </button>
                    <span className="qty">{quantities[item.id]}</span>
                    <button
                      data-text=" "
                      className="button qty-btn"
                      onClick={(e) => {
                        e.preventDefault();
                        e.currentTarget.focus();
                        handleIncrease(item.id);
                      }}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          e.preventDefault();
                          handleText('', false);
                          setTimeout(() => handleIncrease(item.id), 100);
                        }
                      }}
                    >
                      <div className="background dynamic">
                        <span className="content label">+</span>
                      </div>
                    </button>
                  </div>
                  <span className="order-price">
                    {Number(item.price * quantities[item.id]).toLocaleString()}
                  </span>
                  <button
                    data-text=""
                    className="button delete-btn"
                    onClick={(e) => {
                      e.preventDefault();
                      e.currentTarget.focus();
                      handleTouchDelete(item.id);
                    }}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') {
                        e.preventDefault();
                        handleText('', false);
                        setTimeout(() => handleTouchDelete(item.id), 100);
                      }
                    }}
                  >
                    <div className="background dynamic">
                      <span className="content icon" aria-hidden="true">
                        <img src="/images/trash.png" alt="" />
                      </span>
                    </div>
                  </button>
                </div>

                <div className="row-line"></div>
              </div>
            );
          })}
        </div>
        <div
          className="pagination2"
          ref={sections.bottom}
          data-text={`, , ${totalPages}  ${pageNumber},   ,`}
        >
          <button data-text=" ," className="button" onClick={(e) => { e.preventDefault();e.target.focus(); handlePrevPage(); }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(handlePrevPage, 100);
              }
            }}>
            <div className="background dynamic">
              <span className="content label">&lt;&nbsp; </span>
            </div>
          </button>
          <span className="pagination-page-number">
            <span
              className={isDark ? "pagination-page-number-highlight" : "pagination-page-number-default"}
            >
              {pageNumber}
            </span>
            <span className="pagination-separator">&nbsp;/&nbsp;</span>
            <span className="pagination-separator">{totalPages}</span>
          </span>
          <button data-text=" ," className="button"
          onClick={(e) => { e.preventDefault();e.target.focus(); handleNextPage(); }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(handleNextPage, 100);
              }
            }}>
            <div className="background dynamic">
              <span className="content label"> &nbsp;&gt;</span>
            </div>
          </button>
        </div>
      </div>
    </>
  );
};

export default ThirdPage;
