import React, { useContext, useEffect } from "react";
import { AppContext } from "../context/AppContext";
import TakeInIcon from "../components/icons/TakeInIcon";
import TakeOutIcon from "../components/icons/TakeOutIcon";
import Button from "../components/Button";
import { useKeyboardNavigation } from "../assets/useKeyboardNavigation";
import { useTextHandler } from '../assets/tts';
import { startIntroTimer } from "../assets/timer";
import { commonScript } from "../constants/commonScript";

const FirstPage = () => {
  const {
    sections,
    setCurrentPage,
    volume,
    setisDark,
    setVolume,
    setisLarge,
    setisLow
  } = useContext(AppContext);
  const { handleText } = useTextHandler(volume);

  // useKeyboardNavigation
  useKeyboardNavigation({
    initFocusableSections: ["page", "middle", "bottomfooter"],
    initFirstButtonSection: "page",
  });

  // FirstPage   TTS   
  useEffect(() => {
    const timer = setTimeout(() => {
      //  
      if (document.activeElement) {
        document.activeElement.blur();
      }
      // TTS 
      handleText(commonScript.intro);
      //   
      startIntroTimer(commonScript.intro, handleText, () => {
        setisDark(false);
        setVolume(1);
        setisLarge(false);
        setisLow(false);
      });
    }, 200);

    return () => clearTimeout(timer);
  }, [handleText, setisDark, setVolume, setisLarge, setisLow]);

  return (
    <div
      className="main first"
    >
        <img
          className="first-image"
          src="public/images/poster.svg"
          alt="coffee"
        />
        <div 
          className="task-manager"
          data-text=",  ,"
          ref={sections.middle}
        >
          <Button
            className="home-btn"
            dataText=""
            icon={<TakeOutIcon />}
            label=""
            onClick={(e) => {
              e.preventDefault();
              setCurrentPage('second');
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  setCurrentPage('second');
                }, 100);
              }
            }}
          />
          <Button
            className="home-btn"
            dataText=""
            icon={<TakeInIcon />}
            label=""
            onClick={(e) => {
              e.preventDefault();
              setCurrentPage('second');
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  setCurrentPage('second');
                }, 100);
              }
            }}
          />
        </div>
    </div>
  );
};

export default FirstPage;
